
import { defineComponent } from "vue";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import VueDatePicker from '@vuepic/vue-datepicker';
import { PAGE_SIZE } from "@/shared/constants/common.const";
import '@vuepic/vue-datepicker/dist/main.css'
import { BackendUserInformation, RPT203000211_REQ, RPT203000211_RES } from "@/shared/type/RPT202001SS12.type";
import { ExportExcel } from "@/shared/services/ExportExcelService";
import { ExportSheet } from "@/shared/type/ExportExcel.type";
const excel = new ExportExcel()

export default defineComponent({
    name: "rpt202001ss12",
    components: { Pagination, VueDatePicker },

    data() {
        return {
            date: '' as unknown,
            header: [
                { columnName: 'No.' },
                { columnName: 'Reference No.' },
                { columnName: 'User Name' },
                { columnName: 'User Role' },
                { columnName: 'Department' },
                { columnName: 'Email ID' },
                { columnName: 'Phone Number' },
                { columnName: 'Branch' },
                { columnName: 'Address' },
                { columnName: 'User Type' },
                { columnName: 'Authorization Status' },
                { columnName: 'Created By' },
                { columnName: 'Created Date' },
                { columnName: 'Authorized By' },
                { columnName: 'Authorized Date' },
                { columnName: 'Updated by' },
                { columnName: 'Updated Date' },
                { columnName: 'Authorized By' },
                { columnName: 'Authorized Date' },
            ],
            table: [] as BackendUserInformation[],
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            isResetPage: false,
        }
    },

    computed: {
        formattedDate(): string {
            return this.date ? this.$datetime.formatVueDatePicker(this.date as Date) : ''
        },
    },

    mounted() {
        this.getBackendUserInfoDetail(false);
    },

    methods: {
        async getBackendUserInfoDetail(isSearch: boolean) {
            const req = {
                firstRegisterDate: this.date ? this.$datetime.convertDateToString(this.date as Date) : '',
                lastChangeDate: this.date ? this.$datetime.convertDateToString(this.date as Date) : '',
                pageSize: this.pageSize,
                pageNumber: isSearch ? 0 : this.currentPage,
            } as RPT203000211_REQ

            if (isSearch) {
                this.currentPage = 1
                this.isResetPage = true;
            }

            const res = await this.$request.getRequest("RPT20300211", req, false) as RPT203000211_RES;

            this.table = this.onMappingTableData(res.body.list)
            this.totalItems = res.body.totalCount
        },

        async exportToExcel() {
            const customHeader = [
                "AuthorizationNo",
                "UserName",
                "User Role Name",
                "Job Title Code",
                "Email Address",
                "Cell Phone No.",
                "Branch Code",
                "Address",
                "User Type Code",
                "Authorization Status Code",
                "Create By",
                "First Register Date",
                "Authorizer Employee No.",
                "Authorization Date",
                "Update By",
                "Last Change Date",
                "Last Authorizer Employee No.",
                "Last Authorization Date"
            ]

            const req = {
                firstRegisterDate: this.date ? this.$datetime.convertDateToString(this.date as Date) : '',
                lastChangeDate: this.date ? this.$datetime.convertDateToString(this.date as Date) : '',
                pageSize: this.totalItems,
                pageNumber: 0,
            }
            const res = await this.$request.getRequest("RPT20300211", req, false) as RPT203000211_RES;

            const tableForExport = this.onMappingTableData(res.body.list)

            const dataForExport: ExportSheet = {
                data: tableForExport,
                columnHeader: [customHeader],
                sheetName: 'Backend User Information'
            }
            excel.exportSheet([dataForExport], 'Backend User Information Report')
        },

        onMappingTableData(lists: BackendUserInformation[]): BackendUserInformation[] {
            return lists.map((item: BackendUserInformation) => ({
                authorizationNo: item.authorizationNo,
                userName: item.userName,
                userRoleName: item.userRoleName,
                jobTitleCode: item.jobTitleCode,
                emailAddress: item.emailAddress,
                cellPhoneNo: this.$format.formatPhoneNumber(item.cellPhoneNo),
                branchCode: item.branchCode,
                address: item.address,
                userTypeCode: `${item.userTypeCode ? item.userTypeCode + ' : ' : ''} ${this.$codeConverter.convertValue('UserTypeCode', item.userTypeCode)}`,
                authorizationStatusCode: `${item.authorizationStatusCode ? item.authorizationStatusCode + ' : ' : ''} ${this.$codeConverter.convertValue('AuthorizationStatusCode', item.authorizationStatusCode)}`,
                createBy: item.createBy,
                firstRegisterDate: this.$datetime.formatStringToStringDate(item.firstRegisterDate),
                authorizerEmployeeNo: item.authorizerEmployeeNo,
                authorizationDate: this.$datetime.formatStringToStringDate(item.authorizationDate),
                updateBy: item.updateBy,
                lastChangeDate: this.$datetime.formatStringToStringDate(item.lastChangeDate),
                lastAuthorizerEmployeeNo: item.lastAuthorizerEmployeeNo,
                lastAuthorizationDate: this.$datetime.formatStringToStringDate(item.lastAuthorizationDate)
            }));
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.currentPage = pageNumber;
            this.getBackendUserInfoDetail(false);
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        handleReset() {
            this.date = '' as unknown
            this.currentPage = 1
            this.isResetPage = true
            this.getBackendUserInfoDetail(false);
        }
    }
})
